.detail {
	position: relative;
	width: 1024px;
	margin: 0 auto;
	border:1px solid #ccc;
	margin-bottom: 3px;
	background: #fff;
}
.detail_list{
	position: relative;
	line-height: 60px;
	border-bottom: 1px dashed #ccc;
	text-align: center;
	font-size: 16px;
	color:#007dc6;
}
.detail-content{
	width: 600px;
	padding: 20px;
}
.audio{
	position: absolute;
	top:10px;
	right:0;
	display: none;
}
.music_img{
	position: absolute;
	top:20px;
	right:28px;
	width: 30px;
	height: 30px;
}



