.index_item{
	width: 1024px;
	margin: 0 auto;
	position: relative;
	overflow: hidden;
}
.index_page{
	width: 1024px;
	margin: 0 auto;
	border:1px solid #f44;
}
.index_title{
	line-height: 25px;
    background: #f44;
    color: #fff;
    font-size: 12px;
    text-align: left;
    padding-left: 15px;
}
.index_list{
	padding-top:10px;
	background: #fff;
}
.index_content{
	height: 22px;
	font-size: 12px;
	margin-left: 10px;
	color:#333;
}
.index_content a{
	color:#049;
}
.frient_link{
	overflow: hidden;
	width: 1024px;
	margin: 0 auto;
	border:1px solid #333;
	margin-top:3px;
	margin-bottom:3px;
	background: #fff;
}
.frient_icon{
	font-size: 12px;
    background: #0066cc;
    line-height: 25px;
    color: #fff;
    padding-left: 15px;
}
.frient_item{
    width: 84px;
    float: left;
    line-height: 25px;
    text-align: center;
    color: #3D81F2;
    font-size: 12px;
}
.clock{
	position: absolute;
	top: -40px;
	right:-55px;
	width: 500px;
	height:500px;

}
.histogram{
	position: absolute;
	bottom:90px;
	right: 20px;
	height:400px;
	width: 400px;


}




