.header_top{
	height: 32px;
	background:url("../../statics/images/top_title.jpg");
}
.header_title{
	width: 1024px;
	margin:0 auto;
	font-size: 12px;
	line-height: 32px;
}
.header_title_list{
	background:url("../../statics/images/header_link_rightboder.png") no-repeat right;
	background-repeat: no-repeat;
	padding: 0 8px;
	float: left;
}
.serch-keycode{
	margin-right:4px;
}
.header_search{
	float: right;
}
.serch-submit{
	width: 60px;
	height: 22px;
	margin-left: 4px;
	border: 1px solid #aaa;
	background: #fff;
	cursor: pointer;
}
.header_list{
	overflow: hidden;
	width: 1024px;
	height: 70px;
	background: #FFF;
	margin: 0 auto;
}
.header_img{
	float: left;
	height: 60px;
	margin: 5px 0 0 60px;
}
.header_list_item{
	width:700px;
	float: right;
}
.header_list_content{
	float: left;
	padding: 0 10px;
	margin-top:16px;
	border-right: 1px solid #666;
	font-size: 12px;
}
.header_list_content a{
	color:#666;
}
.header_list_content:hover a{
	color:orange;
}
.navblock{
	overflow: hidden;
	width: 1024px;
	margin: 0 auto;
	border: 1px solid #FFE9D3;

}
.navblock div:nth-of-type(1){
	width: 115px;
	float: left;
	text-align: center;
	background: #fff;
}
.navblock div:nth-of-type(2){
	width: 425px;
	float: left;
	border-left: 1px solid #FFE9D3;
	border-right: 1px solid #FFE9D3;
	background: #fff;
}
.navblock div:nth-of-type(3){
	width: 482px;
	float: left;
	background: #fff;
}
.nav_title{
	height: 26px;
	line-height: 26px;
	font-size: 12px;
	color:#666;
	background: #fdf4e5;
	text-align: center;
	border-bottom: 1px solid #FFE9D3;
	cursor:pointer;

}
.nav_title:hover{
	color:orange;
}
.nav_list{
	float: left;
	width: 20%;
	height: 26px;
	text-align: center;
	line-height: 26px;
	font-size: 12px;
	cursor: pointer;
}
.nav_list:hover{
	color:orange;
}
.navblock div:nth-of-type(1) .nav_list{
	width:100%;
}
.tags{
	margin: 0 auto;
	width: 1024px;
	border: 1px solid #ccc;
	margin-bottom: 5px;
	margin-top:5px;
	background: #fff;
}
.tags_title{
	width: 63px;
	float: left;
	display: inline-block;
	padding: 0px 10px;
	line-height: 25px;
	font-weight: 700;
	font-size: 12px;
	color: #888;
	}
.tages_list{
	padding: 5px 10px;
	font-size: 12px;
	overflow: hidden;
}
.tags_icon{
	background: #ccc;
}
.tags_item{
	display: inline-block;
	border: 1px solid #06c;
	border-radius: 15px;
	width: 200px;
	text-align: center;
	margin: 0 10px;
}
.tags_item:hover{
	background: #06c;
	color: #fff;
}
.header_footer{
	width: 1024px;
	margin: 0 auto;
	border:1px solid #aaa;
	background: #f8f8f8;
}
.header_footer_icon{
	text-align: center;
	font-size:12px;
	color:#333;
	line-height:30px;
}
.header_footer_icon1{
	color: orange;
}


