.list{
	width:1024px;
	margin: 0 auto;
}
.list_item{
    height: 133px;
    background: #fff;
    overflow: hidden;
}
.list_img1{
	height: 100%;
	float: left;
	border:none;
	margin: 0
}
.list_img2{
	float: right;
	width: 483px;
	height: 124px
}
.list_nav{
    height: 45px;
    background: #EDF8FC url('../../statics/images/studio_nav_bg.png') no-repeat;;
}
.list_icon{
	margin-left:110px;
}
.list_list{
	width:146px;
	float: left;
    line-height: 45px;
    font-size: 18px;
    text-align: center;
    color: #cc0000;
    text-decoration: none;
    font-family: microsoft yahei;
}
.list_list1{
    composes:list_list;
	width: 164px;
}
.list_content{
	overflow: hidden;
	width: 1024px;
    margin: 0 auto;
    background: #fff;
    font-family: microsoft yahei;
}
.list_content-left{
	position: relative;
    width: 660px;
    float: left;
    margin: 30px 10px 10px 10px;
    border: 3px solid #ec0012;
    border-radius: 20px;
}
.list_left_Img{
	position: absolute;
	left: 20px;
	top:-20px;
}
.list_left_Img2{
	margin: 55px 0 0 35px;
}
.list_title{
	font-size: 20px;
    margin: 10px 30px 0 30px;
    padding: 0 0 10px 0;
    color: #333;
    border-bottom: 1px dotted #ccc;
}
.list_contentList{
	list-style-type: disc;
    margin: 5px 10px 10px 40px;
}
.list_content1{
	font-size: 14px;
	height: 30px;
    line-height: 30px;
}
.list_content_right{
    width: 300px;
    margin: 30px 10px 10px 0;
    float: right;
    border: 3px solid #ec0012;
    border-radius: 20px;
}
.list_content_right_item{
	font-size: 16px;
	color:green;
	margin:10px;
}


